import '../Styles/ProjectionsStyles.css';
import { Component } from 'preact';
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import LateralProjectionComponent from './LateralProjectionComponent.js';
import FrenteProjectionComponent from './FrenteProjectionComponent.js';
import BaseProjectionComponent from './BaseProjectionComponent.js';


class ProjectionsComponent extends Component {

  constructor(props){
    super(props)
    this.state = {
    }
  }

  render(){
    return (
      <div class="row" style={{marginTop:"30px"}}>
      <div class=" col-sm-4">
      <LateralProjectionComponent  data={this.props.data} detritos_data={this.props.detritos_data} selecc_detrital={this.props.selecc_detrital} selecc_tree={this.props.selecc_tree} selecc_umap={this.props.selecc_umap} highlighted_data={this.props.highlighted_data}  callback_highlighted_data={this.props.callback_highlighted_data} callback_selecc_umap={this.props.callback_selecc_umap} clkSeleccDetrital={this.props.clkSeleccDetrital}/>
      </div>
      <div class=" col-sm-4">
      <FrenteProjectionComponent data={this.props.data} detritos_data={this.props.detritos_data} selecc_detrital={this.props.selecc_detrital} selecc_tree={this.props.selecc_tree} selecc_umap={this.props.selecc_umap} highlighted_data={this.props.highlighted_data} callback_highlighted_data={this.props.callback_highlighted_data} callback_selecc_umap={this.props.callback_selecc_umap} clkSeleccDetrital={this.props.clkSeleccDetrital}/>
      </div>
      <div class=" col-sm-4">
      <BaseProjectionComponent data={this.props.data} detritos_data={this.props.detritos_data} selecc_detrital={this.props.selecc_detrital} selecc_tree={this.props.selecc_tree} selecc_umap={this.props.selecc_umap} highlighted_data={this.props.highlighted_data} callback_highlighted_data={this.props.callback_highlighted_data} callback_selecc_umap={this.props.callback_selecc_umap} clkSeleccDetrital={this.props.clkSeleccDetrital}/>
      </div>
      </div>
    );
  }

}
export default ProjectionsComponent;
