import '../Styles/TableStyles.css';
import { Component } from 'preact';
import Button from 'react-bootstrap/Button';
import Table from 'react-bootstrap/Table';
import Pagination from 'react-js-pagination';
import Checkbox from "@material-ui/core/Checkbox";
import Switch from 'preact-material-components/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";

class TableComponent extends Component {

  constructor(props){
    super(props)
    this.state = {
      activePage : 1, //pagina activa inicial en pagination
      itemsCountPerPage:1000, //items por pagina en pagination
      tableColumns:[], //columnas de tabla
      tableRows:[], //filas de tabla
      data:[], //data total en tabla
      switch:true,
    }
    this.mount_Table = this.mount_Table.bind(this);
    this.update_Table = this.update_Table.bind(this);
    this.highlightPoints = this.highlightPoints.bind(this);
  }

  //se invoca antes de renderizar cuando se reciben nuevas propiedades o estados
  componentWillReceiveProps(nextProps) {
    this.update_Table(nextProps);
  }

  /*
  * Método para resaltar puntos (check button)
  */
  highlightPoints(clase,item){
    if(clase=="Detrital"){
      this.props.clkSeleccDetrital(item);
    }
    else {
      var highlighted_data= this.props.highlighted_data;
      if(highlighted_data.includes(item)){
        highlighted_data = highlighted_data.filter((i) => i !== item)
        this.props.callback_highlighted_data(highlighted_data);
      }

      var selecc_umap= this.props.selecc_umap;
      if(selecc_umap.includes(item)){
        selecc_umap = selecc_umap.filter((i) => i !== item)
        this.props.callback_selecc_umap(selecc_umap);
      }

    }

  }

  /*
  * Dibujo las columnas de la tabla
  */
  mount_Table(props){
    var tableColumns = [];

    var arrColumns = ['Ref','Category','SubCategory','SiO2','TiO2','V2O3','Al2O3','Cr2O3','Fe2O3','FeO','MnO','MgO','CaO','Na2O',
    'K2O','ZnO','NiO','Si','Ti','Al','Cr','V','Fe3','Fe2','Mn','Mg','Zn','Ni',
    'MgAl2O4','FeAl2O4','MnAl2O4','ZnAl2O4','MgFe2O4','FeFe2O4','MnFe2O4','ZnFe2O4','NiFe2O4','MgCr2O4',
    'FeCr2O4','MnCr2O4','ZnCr2O4','MgV2O4','FeV2O4','MnV2O4','Mg2TiO4','Fe2TiO4','SiMg2O4','SiFe2O4'
    ];

    //Completo las columnas
    tableColumns.push(<th>#</th>);
    tableColumns.push(<th>remove</th>);
    {Array.from({ length: arrColumns.length }).map((_, index) => (
      tableColumns.push(<th key={index} id={arrColumns[index]}>{arrColumns[index]}</th>)))}
      this.setState({tableColumns:tableColumns});
    }

    /*
    * Dibujo las filas de la tabla
    */
    update_Table(props){
      this.mount_Table(props);
      const arr = [];
      const arr_filterHigligthed = [];
      var data = props.data;
      var tableRows = [];
      var scaleColor = d3.scaleOrdinal()
      .domain(["Alaskan ultramafic","Alkali/Lamprophyres","Basalt","Komatiite","Laterite","Layered intrusion","Metamorphic","Ophiolite","Xenoliths","Detrital"])
      .range(["#7986cb","#f06292", "#ff8a65", "#ffb74d", "#fff176", "#aed581", "#4db6ac","#64b5f6", "#ba68c8","#000000"])
      //.range(["#4D69DC","#9e0142", "#e95c47", "#fdbf6f", "#ffffbe", "#bfe5a0", "#54aead", "#5e4fa2", "#DC4DD8"])

      for(let i=0; i<props.selecc_detrital.length; i++){
        arr_filterHigligthed.push([
          props.detritos_data[props.selecc_detrital[i]].i,
          props.detritos_data[props.selecc_detrital[i]].Ref,
          "Detrital", //Category
          "",//Subcategory
          +props.detritos_data[props.selecc_detrital[i]].SiO2,
          +props.detritos_data[props.selecc_detrital[i]].TiO2,
          props.detritos_data[props.selecc_detrital[i]].V2O3,
          props.detritos_data[props.selecc_detrital[i]].Al2O3,
          props.detritos_data[props.selecc_detrital[i]].Cr2O3,
          props.detritos_data[props.selecc_detrital[i]].Fe2O3,
          props.detritos_data[props.selecc_detrital[i]].FeO,
          props.detritos_data[props.selecc_detrital[i]].MnO,
          props.detritos_data[props.selecc_detrital[i]].MgO,
          props.detritos_data[props.selecc_detrital[i]].CaO,
          props.detritos_data[props.selecc_detrital[i]].Na2O,
          props.detritos_data[props.selecc_detrital[i]].K2O,
          props.detritos_data[props.selecc_detrital[i]].ZnO,
          props.detritos_data[props.selecc_detrital[i]].NiO,
          props.detritos_data[props.selecc_detrital[i]].Si,
          props.detritos_data[props.selecc_detrital[i]].Ti,
          props.detritos_data[props.selecc_detrital[i]].Al,
          props.detritos_data[props.selecc_detrital[i]].Cr,
          props.detritos_data[props.selecc_detrital[i]].V,
          props.detritos_data[props.selecc_detrital[i]].Fe3,
          props.detritos_data[props.selecc_detrital[i]].Fe2,
          props.detritos_data[props.selecc_detrital[i]].Mn,
          props.detritos_data[props.selecc_detrital[i]].Mg,
          props.detritos_data[props.selecc_detrital[i]].Zn,
          props.detritos_data[props.selecc_detrital[i]].Ni,
          props.detritos_data[props.selecc_detrital[i]].MgAl2O4,
          props.detritos_data[props.selecc_detrital[i]].FeAl2O4,
          props.detritos_data[props.selecc_detrital[i]].MnAl2O4,
          props.detritos_data[props.selecc_detrital[i]].ZnAl2O4,
          props.detritos_data[props.selecc_detrital[i]].MgFe2O4,
          props.detritos_data[props.selecc_detrital[i]].FeFe2O4,
          props.detritos_data[props.selecc_detrital[i]].MnFe2O4,
          props.detritos_data[props.selecc_detrital[i]].ZnFe2O4,
          props.detritos_data[props.selecc_detrital[i]].NiFe2O4,
          props.detritos_data[props.selecc_detrital[i]].MgCr2O4,
          props.detritos_data[props.selecc_detrital[i]].FeCr2O4,
          props.detritos_data[props.selecc_detrital[i]].MnCr2O4,
          props.detritos_data[props.selecc_detrital[i]].ZnCr2O4,
          props.detritos_data[props.selecc_detrital[i]].MgV2O4,
          props.detritos_data[props.selecc_detrital[i]].FeV2O4,
          props.detritos_data[props.selecc_detrital[i]].MnV2O4,
          props.detritos_data[props.selecc_detrital[i]].Mg2TiO4,
          props.detritos_data[props.selecc_detrital[i]].Fe2TiO4,
          props.detritos_data[props.selecc_detrital[i]].SiMg2O4,
          props.detritos_data[props.selecc_detrital[i]].SiFe2O4,

        ]);
      }
      if(props.selected_data.length>0){ //Si hay elemento seleccionados en las otras vistas
        for (let j = 0; j < props.selected_data.length; j++) {
            arr_filterHigligthed.push([
              +data[props.selected_data[j]].i,
              +data[props.selected_data[j]].Ref,
              data[props.selected_data[j]].Category1, //Category
              data[props.selected_data[j]].Category2,//Subcategory
              +data[props.selected_data[j]].SiO2,
              +data[props.selected_data[j]].TiO2,
              +data[props.selected_data[j]].V2O3,
              +data[props.selected_data[j]].Al2O3,
              +data[props.selected_data[j]].Cr2O3,
              +data[props.selected_data[j]].Fe2O3,
              +data[props.selected_data[j]].FeO,
              +data[props.selected_data[j]].MnO,
              +data[props.selected_data[j]].MgO,
              +data[props.selected_data[j]].CaO,
              +data[props.selected_data[j]].Na2O,
              +data[props.selected_data[j]].K2O,
              +data[props.selected_data[j]].ZnO,
              +data[props.selected_data[j]].NiO,
              +data[props.selected_data[j]].Si,
              +data[props.selected_data[j]].Ti,
              +data[props.selected_data[j]].Al,
              +data[props.selected_data[j]].Cr,
              +data[props.selected_data[j]].V,
              +data[props.selected_data[j]].Fe3,
              +data[props.selected_data[j]].Fe2,
              +data[props.selected_data[j]].Mn,
              +data[props.selected_data[j]].Mg,
              +data[props.selected_data[j]].Zn,
              +data[props.selected_data[j]].Ni,
              +data[props.selected_data[j]].MgAl2O4,
              +data[props.selected_data[j]].FeAl2O4,
              +data[props.selected_data[j]].MnAl2O4,
              +data[props.selected_data[j]].ZnAl2O4,
              +data[props.selected_data[j]].MgFe2O4,
              +data[props.selected_data[j]].FeFe2O4,
              +data[props.selected_data[j]].MnFe2O4,
              +data[props.selected_data[j]].ZnFe2O4,
              +data[props.selected_data[j]].NiFe2O4,
              +data[props.selected_data[j]].MgCr2O4,
              +data[props.selected_data[j]].FeCr2O4,
              +data[props.selected_data[j]].MnCr2O4,
              +data[props.selected_data[j]].ZnCr2O4,
              +data[props.selected_data[j]].MgV2O4,
              +data[props.selected_data[j]].FeV2O4,
              +data[props.selected_data[j]].MnV2O4,
              +data[props.selected_data[j]].Mg2TiO4,
              +data[props.selected_data[j]].Fe2TiO4,
              +data[props.selected_data[j]].SiMg2O4,
              +data[props.selected_data[j]].SiFe2O4,
            ]);

        }
      }
      this.setState({data:arr_filterHigligthed});

      //Completo las filas
      var row = [];
      var end = this.state.itemsCountPerPage;
      if(arr.length<end) end = arr.length;
      let color;
      if(this.state.switch) {//solo muestro los resaltados
        for(let i = 0; i < arr_filterHigligthed.length; i++){
          color = (arr_filterHigligthed[i][2]!='Detrital' && props.highlighted_data.includes(arr_filterHigligthed[i][0]))? "red" : "black";
          row.push(<td><text style={{color:color}}>{arr_filterHigligthed[i][0]}</text></td>);
          row.push(<td>  <Button variant="outline-dark" style={{border:"none"}} onClick={() => this.highlightPoints(arr_filterHigligthed[i][2],arr_filterHigligthed[i][0])}>
              <FontAwesomeIcon icon={faTrash} />
          </Button> </td>);          for(let j = 1; j< arr_filterHigligthed[i].length;j++){
            if(j==2){ //Category (escribo la categoria con el color correspondiente)
              row.push(<td key={i+""+j} style={{backgroundColor:scaleColor(arr_filterHigligthed[i][j]), color:"#FFFFFF"}}> {arr_filterHigligthed[i][j]}</td>);
            }
            else {
              row.push(<td key={i+""+j}> {arr_filterHigligthed[i][j]}</td>);
            }
          }

          tableRows.push(<tr key={"f"+i}>{row}</tr>);
          row = [];
        }
      }

      this.setState({tableRows:tableRows});

      //Dibujo los gráficos de las columnas
      var arrColumns = ['SiO2','TiO2','V2O3','Al2O3','Cr2O3','Fe2O3','FeO','MnO','MgO','CaO','Na2O',
      'K2O','ZnO','NiO','Si','Ti','Al','Cr','V','Fe3','Fe2','Mn','Mg','Zn','Ni',
      'MgAl2O4','FeAl2O4','MnAl2O4','ZnAl2O4','MgFe2O4','FeFe2O4','MnFe2O4','ZnFe2O4','NiFe2O4','MgCr2O4',
      'FeCr2O4','MnCr2O4','ZnCr2O4','MgV2O4','FeV2O4','MnV2O4','Mg2TiO4','Fe2TiO4','SiMg2O4','SiFe2O4'
      ];
      //Actualizar los boxplots de columnas!!
      var dd = [];

      for(let i = 0; i<arrColumns.length; i++){
        // append the svg object to the body of the page
        dd = [];
        for(let j = 0; j<arr_filterHigligthed.length;j++){
          dd.push(arr_filterHigligthed[j][i+4]);
        }

        d3.select("#"+arrColumns[i]).selectAll("*").remove()

        if(dd.length>0){
          var min = d3.min(dd);
          var max = d3.max(dd);

          var margin = { top: 10, right: 15, bottom: 15, left: 15 },
          width = 120 - margin.left - margin.right,
          height = 60 - margin.top - margin.bottom;

          var dataDomain = [0, 100];

          var x = d3.scaleLinear()
          .domain(dataDomain)
          .rangeRound([0, width])
          // make x domain start and end nice
          .nice();

          var numBins = 100;
          var thresholds = x.ticks(numBins);

          // add the data domain bounds to the thresholds
          //thresholds.concat(dataDomain)
          //thresholds.sort(d3.ascending);

          var bins = d3.histogram()
          .domain(x.domain())
          .thresholds(thresholds)
          (dd);

          // the value is the height
          bins = bins.map(function(d) {
            d.value = d.length;
            return d;
          })

          var y = d3.scaleLinear()
          .domain([0, d3.max(bins, function(d) { return d.value; })])
          .range([height, 0]);

          var svg = d3.select("#"+arrColumns[i])
          .append("svg")
          .attr("width", width + margin.left + margin.right)
          .attr("height", height + margin.top + margin.bottom)
          .append("g")
          .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

          var bar = svg.selectAll(".bar")
            .data(bins)
            .enter().append("g")
            .attr("class", "bar")
            .attr("transform", function(d) {return "translate(" + x(d.x0) + "," + y(d.value) + ")";});

          bar.append("rect")
            .attr("x", 0)
            .style("fill", function(d,i){ return "#969696"; })
          .attr("width", function(d) { return (x(d.x1) - x(d.x0) )+1; })
          .attr("height", function(d) { return height - (y(d.value)); })


          svg.append("g")
          .attr("class", "axis axis--x axisTable")
          .attr("transform", "translate(0," + height + ")")
          .call(d3.axisBottom(x).ticks(2).tickValues([0,100]).tickFormat(d3.format(".2f")).tickSizeInner(3).tickSizeOuter(0));
      }
    }
  }

  /*
  * Modifico las filas visibles en la tabla al seleccionar una pagina nueva del pagination
  */
  handlePageChange(pageNumber) {
    var scaleColor = d3.scaleOrdinal()
    .domain(["Alaskan ultramafic","Alkali/lamprophyres","Basalt","Komatiite","Laterite","Layered intrusion","Metamorphic","Ophiolite","Xenoliths"])
    .range(["#4D69DC","#9e0142", "#e95c47", "#fdbf6f", "#ffffbe", "#bfe5a0", "#54aead", "#5e4fa2", "#DC4DD8"])

    //Completo las filas
    var row = [];
    var arr = this.state.data;
    var tableRows = [];
    let color ="black";
    var end = pageNumber*this.state.itemsCountPerPage;
    if(arr.length<end) end = arr.length;
    for(let i = (pageNumber-1)*this.state.itemsCountPerPage; i < end; i++){
      if(arr_filterHigligthed[i][2]!='Detrital' && this.props.highlighted_data.includes(arr_filterHigligthed[i][0]))
        color = "red";
      row.push(<td><text style={{color:color}}>{arr_filterHigligthed[i][0]}</text></td>);
      //row.push(<td> <Checkbox checked={this.props.highlighted_data.includes(arr_filterHigligthed[i][0])} onClick={() => this.highlightPoints(arr_filterHigligthed[i][2],arr_filterHigligthed[i][0])} /></td>);
      row.push(<td>  <Button variant="outline-dark" style={{border:"none"}} onClick={() => this.highlightPoints(arr_filterHigligthed[i][2],arr_filterHigligthed[i][0])}>
          <FontAwesomeIcon icon={faTrash} />
      </Button> </td>);

      for(let j = 1; j< arr_filterHigligthed[i].length;j++){
        if(j==2){ //Category (escribo la categoria con el color correspondiente)
          row.push(<td key={i+""+j} style={{backgroundColor:scaleColor(arr_filterHigligthed[i][j]), color:"#FFFFFF"}}> {arr_filterHigligthed[i][j]}</td>);
        }
        else {
          row.push(<td key={i+""+j}> {arr_filterHigligthed[i][j]}</td>);
        }
      }

      tableRows.push(<tr key={"f"+i}>{row}</tr>);
      row = [];
    }

    this.setState({tableRows:tableRows});
    this.setState({activePage: pageNumber});
  }

  render(){
    return (
      <div class="container-Table">
      <Pagination
      activePage={this.state.activePage}
      itemsCountPerPage={this.state.itemsCountPerPage}
      totalItemsCount={this.state.data.length}
      pageRangeDisplayed={10}
      onChange={this.handlePageChange.bind(this)}
      onClick={console.log("")}
      />
      <div class="table-responsive-md">
      <Table bordered hover>
      <thead>
      <tr>
      {this.state.tableColumns}
      </tr>
      </thead>
      <tbody>
      {this.state.tableRows}
      </tbody>
      </Table>
      </div>
      </div>
    );
  }

}
export default TableComponent;
/*
<text class="textSwitch switch-table" >Filter selected data</text>

<Switch  class="switch-table switch" checked={this.state.switch} onChange={e =>{this.setState({switch:!this.state.switch},()=>this.update_Table(this.props)); }}/>

*/
